import React, { Component } from "react";
import { Divider,  Drawer, Form, Button, Col, Row, Input, Select, DatePicker } from 'antd';
import { PlusOutlined, BarcodeOutlined, DownloadOutlined } from '@ant-design/icons';

const { Option } = Select;

export class FormInDrawer extends Component {
  state = { visible: false };

  showDrawer = () => {
    this.setState({
      visible: true,
    });
  };

  onClose = () => {
    this.setState({
      visible: false,
    });
  };


  
  render() {
    return (
      <div>
          <Button icon={<BarcodeOutlined />} onClick={this.showDrawer} type="default" size="small">Enroll Devices</Button>
        
        <Drawer
          title="Enroll Devices in [Profile Name]"
          width={720}
          onClose={this.onClose}
          visible={this.state.visible}
          bodyStyle={{ paddingBottom: 80 }}
          footer={
            <div
              style={{
                textAlign: 'right',
              }}
            >
              <Button
                onClick={this.onClose}
                style={{ marginRight: 8 }}
              >
                Close
              </Button>
              <Button type="primary" icon={<DownloadOutlined />} >
                Download
                </Button>              
            </div>
          }
        >
        <img className="img-fluid" src="/img/others/barcode.jpg" alt="" />
        </Drawer>
        
      </div>
    );
  }
}

export default FormInDrawer;
