import React, { Component } from "react";
import { Divider, Steps, message, Popover, Drawer, notification, Form, Button, Col, Row, Input, Select, DatePicker } from 'antd';
import { PlusOutlined } from '@ant-design/icons';
import FormProfileInfo from './form-profile-info.js';
import FormWifi from './form-wifi.js';
import FormFota from './form-fota.js';
import FormSummary from './form-summary.js';
import DrawerBarcode from './drawer-barcode.js';
import NotificationBarcode from './notification-barcode.js';

const { Option } = Select;
const { Step } = Steps;
const FormProfileInfoSection = () => (
  <FormProfileInfo />

 
)

const steps = [
  {
    title: "Profile Info",
    description: "Enter basic profile information.",
    content: <FormProfileInfo />
  },
  {
    title: "WiFi",
    description: "Provide WiFi info for enrollment.",
    content: <FormWifi />
  },
  {
    title: "FOTA",
    description: "Activate FOTA for this enrollment.",
    content: <FormFota />
  },
  {
    title: "Confirm",
    description: "Confirm your entries before saving.",
    content: <FormSummary/>
  }

];

const customDot = (dot, { status, index }) => (
  <Popover
    content={
      <span>
        step {index} status: {status}
      </span>
    }
  >
    {dot}
  </Popover>
);

export class FormInDrawer extends Component {
  state = { visible: false };

  showDrawer = () => {
    this.setState({
      visible: true,
    });
  };

  onClose = () => {
    this.setState({
      visible: false,
    });
  };

  constructor(props) {
    super(props);
    this.state = {
      current: 0
    };
  }

  next() {
    const current = this.state.current + 1;
    this.setState({ current });
  }

  prev() {
    const current = this.state.current - 1;
    this.setState({ current });
  }
  
  save(){
    this.onClose();
    message
    .loading("Generating barcode..", 2.5)
    .then(() => message.success("Barcode generated", 2.5))
    .then(() => {
        const args = {
          message: "Lab Environment",
          description: <NotificationBarcode/>,
          duration: 0
        };
        notification.open(args);
      }
    

    );

  }


  render() {
    const { current } = this.state;
    return (
      <div>
        <Button type="primary" onClick={this.showDrawer}>
          <PlusOutlined /> New Enrollment Profile
        </Button>
        <Drawer
          title="Create a new enrollment profile"
          width={720}
          onClose={this.onClose}
          visible={this.state.visible}
          bodyStyle={{ paddingBottom: 80 }}
          footer={
            <div
              style={{
                textAlign: 'right',
              }}
            >
          {current < steps.length - 1 && (
            <Button type="primary" onClick={() => this.next()}>
              Next
            </Button>
          )}
          {current === steps.length - 1 && (
            <Button
              type="primary"
              onClick={() => this.save()}
            >
              Save
            </Button>
          )}
          {current > 0 && (
            <Button style={{ marginLeft: 8 }} onClick={() => this.prev()}>
              Previous
            </Button>
          )}
            </div>
          }
        >
          <Steps size="small" current={current} progressDot={customDot}>
            {steps.map(item => (
              <Step key={item.title} title={item.title} title={item.description} />
            ))}
            
          </Steps>
          <Divider />
          <div className="steps-content">{steps[current].content}</div>
        </Drawer>
        
      </div>
      
    );
  }
}

export default FormInDrawer;
